.response-container{
    background-color: white;
    display: flex;
    justify-content:center;
    align-items:center;
    width: 100vw;
}

.response-table{
    background-color: white;
    flex-direction: column;
    display: flex;
    justify-content:center;
    align-items:center;
    width:900px;
    background-color: rgb(224, 224, 224);
}

.one-pagination{
    width:50px;
    height: 30px;
    display: flex;
    justify-content:center;
    align-items:center;
    background-color: rgb(216, 216, 216);
    border-radius: 5px;
    cursor: pointer;
    margin:2%;
}

.pagination:hover{
    background-color: gray;
}

.response-table-one-content{
    padding:10px 5px;
    background-color: white;
    box-shadow: 1px 1px 3px rgba(0,0,0,0.3);
    border-radius: 5px;
}

.response-table-top{
    display: flex;
    justify-content: start;
    align-items: center;
    flex-direction: column;
    font-family: OTWelcomeBA;
    font-weight: 600;
}