.questionspage-container{
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-family: OTWelcomeRA;
}

.q-inner{
    background-color: white;
    display: flex;
    align-items: start;
    justify-content: center;
    flex-direction: column;
    font-family: OTWelcomeRA;
    width:60%;
}

.q-title-top{
    text-align: left;
    font-family: OTWelcomeBA;
    font-size:23px;
    margin-top:10%;
}

.tab-one{
    cursor: pointer;
    color: gray;
    margin-left:4%;
}

.q-button{
    margin-top:6%;
    border:2px solid #6a63f7;
    border-radius: 20px;
    width: 150px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color:#6a63f7;
    font-size:17px;
    font-weight: 500;
    padding-top:3%;
}
.q-button:hover{
    background-color: #6a63f733;
}

.quetions-content-bottom{
    width:100%;
    display: flex;
    align-items: start;
    justify-content: center;
    flex-direction: column;
    margin-top: 4%;
}

.q-tabs{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: start;
    flex-direction: row;
    border-bottom: 1px solid rgba(128, 128, 128, 0.384);
}
.q-one-content{
    margin-top: 2%;
    background-color: rgba(235, 235, 235, 0.3);
    padding:20px 30px;
    border-radius: 30px;
    text-align: left;
    width:100%;
    box-shadow:1px 1px 4px rgba(0,0,0,0.4);
}

.q-content-ask{
    font-weight: 700;
    font-size:17px;
    margin-top:3%;
}
.q-content-answer{
    font-weight: 500;
    font-size:17px;
    margin-top:2%;
}

@font-face {
    font-family: 'ImcreSoojin';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.3/ImcreSoojin.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }
  @font-face {
    font-family: 'OTWelcomeRA';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2110@1.0/OTWelcomeRA.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'OTWelcomeBA';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2110@1.0/OTWelcomeBA.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
  }

  .nav-button-round{
    border-radius:20px;
    width:130px;
    height:35px;
    display: flex;
    justify-content: center;
    align-items : center;
    background-color:#6a63f7;
    margin-left:16vw;
    color:white;
    border:none;
    cursor: pointer;
}

.nav-left-text{
    color:black;
    margin-left:2vw;
    cursor: pointer;
    padding:3px 0px;
    border-radius: 5px;
    text-decoration: none;
}
.nav-left-text:hover{
    background-color: #6a63f75b;
}
.nav-bar-container{
    position: fixed;
    top: 0;
    z-index:3;
    height:60px;
    width: 100%;
    display: flex;
    justify-content: start;
    align-items : center;
    color:rgb(106, 99, 247);
    background-color:white;
}

.nav-bar-container-top{
    position: fixed;
    top: 0;
    z-index:3;
    height:60px;
    width: 100%;
    display: flex;
    justify-content: start;
    align-items : center;
    color:white;
    background-color:rgba(255, 255, 255, 0);
}

.nav-bar-title{
    font-size:36px;
    font-weight: 700;
    text-align:start;
}

.nav-on{
    display: flex;
    align-items :center;
    width:50%;
    cursor:pointer;
    justify-content : center;
    margin-right:5vw;
    width:100%;
}
.nav-button{
    border:1px solid black;
    border-radius:5px;
    width:130px;
    height:35px;
    display: flex;
    justify-content: center;
    align-items : center;
    background-color:#6a63f7;
    margin-left:16vw;
    color:white;
}

.nav-button:hover{
    background-color:#6a63f773;
}


@media screen and (max-width:800px){
    .nav-button{
        margin-left:1vw;
    }

}